import Vue from 'vue'

const srvHost = {
    development: 'https://server.castjp.top',
    production: 'https://server.castjp.top'
};

let mEndPoint = 'https://server.castjp.top/api';
let mHeader = {};

const toQuery = (query) => {
    let retData = '';
    const objArray = Object.entries(query);
    for (const [key, val] of objArray) {
        if (retData) {
            retData += `&${key}=${val}`;
        } else {
            retData += `?${key}=${val}`;
        }
    }
    return retData;
};

const getOptions = () => {
    return {
        headers: {
            ...mHeader
        }
    }
};

export default class HttpSrv {
    constructor(host) {
        if (host) {
            this.host = host;
        }
    }

    reqFinish() {
        this.fullUrl = null;
        this.reqUrl = null;
        this.query = null;
        this.param = null;
        this.body = null;
    }

    handler(result) {
        this.reqFinish();
        return result.data;
    }

    exception(error) {
        this.reqFinish();
        console.log('exception:', error);
    }

    static setEndPoint(endPoint) {
        mEndPoint = endPoint;
    }

    static setHeader(data) {
        mHeader = {
            ...mHeader,
            ...data
        }
    }

    setUrl(url) {
        this.fullUrl = url;
        return this;
    }

    setPath(path) {
        this.reqUrl = mEndPoint + path;
        return this;
    }

    setQuery(query) {
        if (this.reqUrl) {
            this.reqUrl += toQuery(query);
        } else if (this.fullUrl) {
            this.fullUrl += toQuery(query);
        }
        return this;
    }

    setBody(body) {
        this.body = body;
        return this
    }

    getReq() {
        return Vue.axios.get(this.reqUrl || this.fullUrl, getOptions())
            .then(result => result.data).catch(error => exception(error));
    }

    postReq() {
        return Vue.axios.post(this.reqUrl || this.fullUrl, this.body, getOptions())
            .then(result => this.handler(result)).catch(error => this.exception(error));
    }

    putReq() {
        return Vue.axios.put(this.reqUrl || this.fullUrl, this.body, getOptions())
            .then(result => this.handler(result)).catch(error => this.exception(error));
    }

    delReq() {
        return Vue.axios.delete(this.reqUrl || this.fullUrl, getOptions())
            .then(result => this.handler(result)).catch(error => this.exception(error));
    }

    allReq(reqArray) {
        return Vue.axios.all(reqArray).catch(error => this.exception(error));
    }
}



