import Vuex from 'vuex'
import Vue from 'vue'

Vue.use(Vuex);

let store = new Vuex.Store({});
store.registerModule('app', {
  state: {
    count: 10,
    isLoading: false,
    bId:'11'
  },
  mutations: {
    setCount(state, value) {
      state.count = value;
    },
    setBid(state,value){
      state.bId = value;
    },
    updataLoading(state, value) {
      state.isLoading = value;
    }
  },
  actions: {
    setBidAct({commit},id){
      commit('setBid',id);
    },
    setCountAct({commit}, count) {
      commit('setCount', count);
    },
    updataLoadingAct({
      commit
    }, Loading) {
      commit('updataLoading', Loading);
    }
  }
});
export default store;


