<template>
  <div class="myhead">
    <mt-header :title="this.$route.meta.title" class="header" fixed>
      <mt-button @click="goback" icon="back" slot="left">返回</mt-button>
    </mt-header>
  </div>
</template>
<script>
  export default {
    name:'myhead',
    methods:{
      goback(){
        this.$router.go(-1)
      }
    }
  }
</script>
<style type="text/css">
  /*.myhead {
    margin-bottom: 3.5rem;
  }*/
  .header {
    background-color: #E10601;
  }
</style>



