import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router);
export default new Router({
  routes: [
    {
      path: '/',
      redirect:'/bookshelf'
    },
    {
      path: '/choice',
      name: 'choice',
      component: ()=> import('@/components/choice'),
      meta: {
        title: '精选',
        showMeu:true,
        showHead:false,
        keepAlive:true
      }
    },
    {
      path: '/bookshelf',
      name: 'bookshelf',
      component: ()=>import('@/components/bookshelf'),
      meta: {
        title: '书架',
        showMeu:true,
        showHead:false,
        keepAlive:false
      }
    },
    {
      path: '/find',
      name: 'find',
      component: ()=>import('@/components/find'),
      meta: {
        title: '发现',
        showMeu:true,
        showHead:false,
        keepAlive:true
      }
    },
    {
      path: '/my',
      name: 'my',
      component: ()=>import('@/components/my'),
      meta: {
        title: '个人中心',
        showMeu:true,
        showHead:false,
        keepAlive:true
      }
    },
    {
      path: '/classify',
      name: 'classify',
      component: () => import('@/components/classify'),
      meta: {
        title: '分类',
        showMeu:false,
        showHead:true,
        keepAlive:false
      }
    },
    {
      path: '/rankingList',
      name: 'rankingList',
      component: () => import('@/components/rankingList'),
      meta: {
        title: '排行榜',
        showMeu:false,
        showHead:true,
        keepAlive:false
      }
    },
    {
      path: '/bookDetails/:id',
      name: 'bookDetails',
      component: () => import('@/components/bookDetails'),
      meta: {
        title: '详情',
        showMeu:false,
        showHead:false,
         keepAlive:false
      }
    },
    {
      path: '/list/:name',
      name: 'list',
      component: () => import('@/components/list'),
      meta: {
        title: '',
        showMeu:false,
        showHead:true,
        keepAlive:false
      }
    },
    {
      path: '/threeRiver',
      name: 'threeRiver',
      component: () => import('@/components/threeRiver'),
      meta: {
        title: '',
        showMeu:false,
        showHead:false,
        keepAlive:false
      }
    },
    {
      path: '/chapter/:id',
      name: 'chapter',
      component: () => import('@/components/chapter'),
      meta: {
        title: '目录',
        showMeu:false,
        showHead:false,
        keepAlive:false
      }
    },
    {
      path: '/searchBook',
      name: 'searchBook',
      component: () => import('@/components/searchBook'),
      meta: {
        title: '',
        showMeu:false,
        showHead:false,
        keepAlive:true
      }
    },
    {
      path: '/comment/:cid',
      name: 'comment',
      component: () => import('@/components/comment'),
      meta: {
        title: '评论详情',
        showMeu:false,
        showHead:true,
        keepAlive:false
      }
    }
  ],
  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  }
});








