<template>
  <div id="app">
    <myhead v-show="$route.meta.showHead"></myhead>
    <div class="container">
      <keep-alive>
          <router-view v-if="$route.meta.keepAlive"></router-view>
      </keep-alive>
      <router-view v-if="!$route.meta.keepAlive"></router-view>
    </div>
    <meu v-show="$route.meta.showMeu"></meu>
  </div>
</template>

<script>
import meu from '@/components/Meu'
import myhead from '@/components/Head'

export default {
  name: 'App',
  components:{
    meu,
    myhead,
  }
}
</script>

<style>
body {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width: 100%;
  height: 100%;
}
</style>



